export function ts2time(unixTs){
  var date = new Date(unixTs * 1000);

  var hours = "0" +date.getHours();
  var minutes = "0" + date.getMinutes();
  var seconds = "0" + date.getSeconds();
  
  return `${hours.substr(-2)}:${minutes.substr(-2)}:${seconds.substr(-2)}`;

}

export function ts2date(unixTs){
    var date = new Date(unixTs * 1000);
  
    var mon = "0" + (date.getMonth()+1);
    var day = "0" + date.getDate();
    return `${date.getFullYear()}-${mon.substr(-2)}-${day.substr(-2)}`;
}

export function ts2datetime(unixTs, timezone=false){
    let datetime = `${ts2date(unixTs)} ${ts2time(unixTs)}`;
    datetime = timezone ? datetime + ` (${Intl.DateTimeFormat().resolvedOptions().timeZone})` : datetime;
    return datetime;
}
