import React,  {useContext, useState} from 'react'
import { Link } from 'react-router-dom'
import "./css/navagation.css"
import { Navbar, Nav } from 'react-bootstrap'
import {EthContext} from "./EthContext.js"
import { useTopAlert } from './useTopAlert'
import SmallLoadingBar from './SmallLoadingBar'
import {shortAddress} from './util'

// import ReactLoading from 'react-loading';

// export default Navigation = (props) => {
export default function Navigation() {
    const [loginLoading, setLoginLoading] = useState(false);
    const {ethLink, account} = useContext(EthContext);
    const { hide, showMessage: showLoginFail, RenderTopAlert: LoginFail} = useTopAlert() 
    async function loginEth(){
        // setShowError(false);
        hide()
        setLoginLoading(true);
        try {
            await ethLink.login();
        } catch (e) {
            console.log("loginEth(): " + e.name + ': ' + e.message);
            // alert(e.message);
            showLoginFail(e.message);
        }
        setLoginLoading(false);
    }

    async function goContact(){
        document.getElementById('contact-div').scrollIntoView({ behavior: 'smooth', block: 'start' })
    }

    // async function testSetNft(){
    //     let nft = ethLink.getNft();
    //     setNft(nft);
    // }
    
    // console.log("in NAV!!");
    let loginHtml = [];
    if (loginLoading){
        loginHtml.push(<SmallLoadingBar key="login-loadingbar" /> );
    }else{
        if (account!==''){
            // console.log("in account!==''!!");
            let shortAddr = shortAddress(account);
            // console.log("in account!==''!!");
            loginHtml.push(<Nav.Link key="login">{shortAddr}</Nav.Link> );
        }else{
            loginHtml.push(<Nav.Link key="login" onClick={() => loginEth() } >Login</Nav.Link> );
        }
    }


    return (
        <>
        <Navbar collapseOnSelect expand="md" bg="light" style={{padding: '0.5rem 2.5rem'}} >
        <Navbar.Brand as={Link} to="/" style={{fontSize: '2rem'}}  >ethPlant.com</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="ml-auto">
                <Nav.Link key="home" as={Link} to="/" >Home</Nav.Link>
                <Nav.Link key="contact" onClick={() => goContact()} >Contact</Nav.Link>
                {loginHtml}
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      <LoginFail />
     </>
    );
};