import React, { useState, useEffect, useRef } from 'react';
import {useParams} from "react-router-dom";
import LoadingBar from './LoadingBar'
import Album from './Album';
// fontawesome
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCamera, faWater, faVideo, faImages } from '@fortawesome/free-solid-svg-icons'
import { playRemote } from './util.js';
import { VideoPrefix } from "./config.js"; 
import { EthContext } from "./EthContext.js";
import { PlantFeature } from './PlantFeature';
import { useTopAlert } from './useTopAlert'
import ControlPanel from './ControlPanel';


library.add(faCamera)
library.add(faWater)
library.add(faVideo)
library.add(faImages)

const l = console.log;

const Plant = (props) => {
    // react
    const [tokenId, setTokenId] = useState(-1);
    const [owner, setOwner] = useState('0x');
    const [isLoading, setIsLoading] = useState(false);
    const [pc, setPC] = useState(null); // pc: PeerConnection
    const albumRef = useRef();
    const {nft, account, ethLink} = React.useContext(EthContext);
    const [showControl, setShowControl] = useState(true);
    const [showAlbum, setShowAlbum] = useState(false);
    const [showRecord, setShowRecord] = useState(false);
    // react-router
    let { name } = useParams();
    const { showMessage: showFail, RenderTopAlert: ErrorBox} = useTopAlert() 
    
    let  videoId = name + "Video";
    useEffect(() => {
        // l('in useEffect(), nft:' +nft);
        if (nft!=null){
            nft.nftID(name).then(nftId => {
                setTokenId(nftId);
                nft.ownerOf(nftId).then(own => {
                    setOwner(own);
                });
            });
        }
        
        return function cleanup() {
          l('in Plant(%d) cleanup(), name:', name);
        };
    }, [nft, name]);

    useEffect(() => {
        return function cleanup() {
            if (pc!==null && pc!==undefined){
                pc.close();
            }     
        };
    }, [pc]);


    useEffect(() => {
        let get_pc = playRemote(name, videoId);
        setPC(get_pc);
    }, [name, videoId]);

    // useEffect(() => {
    //     document.title = `[${name}] ethPlant.com`;
    // }, [name]);

    async function takePhoto() {
        // scrollToTargetAdjusted("1605016746");
        //  return
        if (ethLink.signer===null || ethLink.signer===undefined){
            showFail("Please login account first.")
            return
        }
        if (tokenId < 0){
            showFail("tokenId NOT ready.")
            return
        }
        if(account!==owner){
            showFail("You are not owner!")
            return
        }
        setIsLoading(true);
        
        let snapshotSign = await ethLink.signer.signMessage("snapshot");
        // l("snapshotSign: "+ snapshotSign);
        const postUrl = VideoPrefix + name + "/photo/take";
        await fetch(postUrl, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({"action":"takephoto", "sign_data":snapshotSign })
        }).then((returnedResponse) => {
            returnedResponse.json().then((res) => {
                console.log("takephoto res: ");
                console.log(res);
                if (res["success"]===true){
                    let albumResult = albumRef.current.addPhoto(res);
                    console.log("albumResult:");
                    console.log(albumResult);
                }else{
                    showFail(res["detail"]);
                }
            });
         }).catch((error) => {
           // Your error is here!
           console.log(error)
         });;

        setIsLoading(false);
        await albumArea();

    }

    async function sendControl(controlType, detail) {
        console.log(`in sendControl(${controlType}, ${detail})`)
        setIsLoading(true);
        const postUrl = VideoPrefix + name + "/control";
        const rawResponse = await fetch(postUrl, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({"action":controlType, "detail": detail, "sign_data":"" })
        });
        const res = await rawResponse.json();
        console.log("sendControl res: ");
        console.log(res);
        if (res["success"]===false){
            showFail(res["detail"]);
        }
        setIsLoading(false);
    }

    async function controlArea(){
        //document.getElementById('watering-div').scrollIntoView({ behavior: 'smooth', block: 'end' })
        setShowControl(true);
        setShowAlbum(false);
        setShowRecord(false);
    }

    async function albumArea(){
        setShowControl(false);
        setShowAlbum(true);
        setShowRecord(false);
    }

    async function recordArea(){
        setShowControl(false);
        setShowAlbum(false);
        setShowRecord(true);
    }

    let albumFethURL = VideoPrefix + name + "/photo/list";
    let pumpFethURL  = VideoPrefix + name + "/record/pump/list";
    let fanFethURL  = VideoPrefix + name + "/record/fan/list";
    
    return (
        
        <div className="row gallery">
            <ErrorBox />
            <div className="col-md-1 col-sm-0" />
            <div className="col-md-10 col-sm-12">
                <LoadingBar show={isLoading} />
                <div className="item">
                    <div className="title">{name}</div>
                    <div id={videoId} className="imgbox imgbox-169"> 
                        {/* <img src={props.img} alt="" />   */}
                    </div>
                    {/*--------------- Menu Row --------------- */}
                    <div className="row">
                        <div className="col-3">
                            <div className="plant-cmd-click" onClick={() => controlArea()}><FontAwesomeIcon icon={["fas", "water"]} /> <b className="fakehref">Watering</b></div>
                        </div>
                        <div className="col-3">
                            <div className="plant-cmd-click" onClick={() => albumArea()}><FontAwesomeIcon icon={["fas", "images"]} /> <b className="fakehref">Album</b></div>
                        </div>
                        <div className="col-3">
                            <div className="plant-cmd-click" onClick={() => recordArea()}><FontAwesomeIcon icon={["fas", "video"]} /> <b className="fakehref">Record</b></div>
                        </div>
                        <div className="col-3">
                            <div className="plant-cmd-click" onClick={() => takePhoto()}><FontAwesomeIcon icon={["fas", "camera"]} /> <b className="fakehref">Take Picture</b></div>
                        </div>
                    </div>
                    {/*--------------- Owner Row --------------- */}
                    <div className="row">
                        <div className="col-3 col-lg-2 col-name">Owner</div>
                        <div className="col-8 col-lg-10">{owner.substring(0,6) + "..." + owner.substring(owner.length-4, owner.length)}</div>
                    </div>
                    {/*--------------- Control Area --------------- */}
                    {showControl ?
                    <ControlPanel name={name} controlHandle={sendControl} />
                    : "" }
                    {/*--------------- Album Area --------------- */}
                    {showAlbum ?
                    <div>
                        <div className="row">
                            <div className="col-3 col-lg-2 col-name">Feature</div>
                            <div className="col-8 col-lg-10">
                                <PlantFeature name={name} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-3 col-lg-2 col-name">Album</div>
                            <div className="col-9 col-lg-10">
                                <Album name={name} ref={albumRef} photoURL={albumFethURL} overlaySuffix="jpg" />
                            </div>
                        </div>
                    </div>
                    : "" }
                    {/*--------------- Record Area --------------- */}
                    {showRecord ?
                    <div>
                        <div className="row">
                            <div className="col-3 col-lg-2 col-name">Watering</div>
                            <div className="col-9 col-lg-10">
                                <Album name={name} photoURL={pumpFethURL} overlaySuffix="mp4" />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-3 col-lg-2 col-name">Fan</div>
                            <div className="col-9 col-lg-10">
                                <Album name={name} photoURL={fanFethURL} overlaySuffix="mp4" />
                            </div>
                        </div>
                    </div>
                    : "" }
                    
                </div>
            </div>
            <div className="col-md-1 col-sm-0" />
        </div>

    );

};

export default Plant;
