import React, { useState } from 'react'

import TopAlert from './TopAlert.js'

// Renders a modal to the modal root and handles the visibility state
// of this modal.
//
// NOTE: Each modal you want to render should use a separate hook!!!
// Otherwise your modals will share their visibility state which might lead
// to overlapping and unclosable elements.
export const useTopAlert = () => {
  const [isVisible, setIsVisible] = useState(false)
  const [msg, setMsg] = useState("")
  
  const showMessage = (msg) => {
    window.scrollTo(0, 0);
    setIsVisible(true);
    setMsg(msg);
    setTimeout(function() {
      hide();
    }, 5000);
  };
  const hide = () => {
    setIsVisible(false)
  }
  
  // const RenderTopAlert = ({ children }: { children: React.ReactChild }) => (
  //   <React.Fragment>
  //     {isVisible && <TopAlert closeModal={hide}>{msg}</TopAlert>}
  //   </React.Fragment>
  // )
  const RenderTopAlert = () => (
    <React.Fragment>
      {isVisible && <TopAlert closeModal={hide}>{msg}</TopAlert>}
    </React.Fragment>
  )

  return {
    showMessage,
    hide,
    RenderTopAlert,
  }
}