import React, { useState, useEffect } from 'react';
const l = console.log;

export const PlantFeature = (props) => {
    const [feature, setFeature] = useState([]);
    useEffect(() => {
        // l("props.name:" + props.name);
        if (props.name!==""){
            fetchFeature(props.name)
        }
    }, [props.name]);

    function fetchFeature(name) {
        let jsonURL = "/data/" + name + ".json";
        fetch(jsonURL)
        .then(res => res.json())
        .then(
            (result) => {
                setFeature(result['features'])
            },
            // Note: it's important to handle errors here
            // instead of a catch() block so that we don't swallow
            // exceptions from actual bugs in components.
            (error) => {
                l("jsonURL:" + jsonURL);
                l("fetchFeature error: ");
                l(error);
            }
        )
    }

    let featureHtml = [];
    let liKey=0;
    for (const featureUnit of feature) {
        liKey++;
        featureHtml.push(<li key={liKey}>{featureUnit}</li>);
    }

    return(
        <ul style={{"paddingLeft": "0rem"}}>
            {featureHtml}
        </ul>
    )

}