import React, { useState, useEffect } from 'react';
import { ethers } from "ethers";

import LoadingBar from './LoadingBar'
// bootstrap
import Button from 'react-bootstrap/Button';
// router
import {Link} from "react-router-dom";
// fontawesome
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEye } from '@fortawesome/free-solid-svg-icons'
import { playRemote } from './util.js';
import { PlantFeature } from './PlantFeature';
import { EthContext } from "./EthContext.js";
import { useTopAlert } from './useTopAlert';
//css
import './css/PlantGridUnit.scss';

library.add(faEye)

const l = console.log;

const PlantGridUnit = (props) => {
    const [name, setName] = useState('');
    const [owner, setOwner] = useState('0x');
    const [price, setPrice] = useState(0);
    const [listSell, setListSell] = useState(false);
    const [listSellPrice, setListSellPrice] = useState(0);
    const [changeRow, setChangeRow] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [pc, setPC] = useState(null);
    const {nft, account} = React.useContext(EthContext);
    const { showMessage: showFail, RenderTopAlert: ErrorBox} = useTopAlert() 
    let tokenId = props.id; // token id
    // let nft = props.nft;
    let videoId = name + "Video";
    // l("in gridunit tokenId=" + tokenId);

    useEffect(() => {
        l('in PlantGridUnit useEffect(), id:' +props.id);
        if (nft===undefined){
            return
        }
        nft.nftName(tokenId).then(nftName => {
            setName(nftName);
        });

        nft.ownerOf(tokenId).then(own => {
            setOwner(own);
        });

        nft.getSale(tokenId).then(price => {
            setPrice(price);
        });

        // Update the document title using the browser API
        return function cleanup() {
        //   l('in PlantItem cleanup(), id:' +props.id);
        };
    }, [nft, tokenId, props.id]);

    useEffect(() => {
        return function cleanup() {
            if (pc!==null && pc!==undefined){
                pc.close();
            }
        };
    }, [pc]);

    useEffect(() => {
        let get_pc = playRemote(name, videoId);
        setPC(get_pc);
    }, [name, videoId]);
    
    async function buyNFT() {
        console.log("account:" + account);
        if (account===''){
            showFail("Please login account first.")
            return
        }
        
        setIsLoading(true);
        try {
            await nft.buy(tokenId, {value: price});
        }catch (error) {   
            console.log("Buy error:" + error);
            setIsLoading(false);
        }  
        await nft.once("NFTBuyEvent", (i_tokenId) => {
            l("in NFTBuyEvent");
            if (parseInt(i_tokenId)===tokenId){
                nft.ownerOf(tokenId).then(own => {
                    setOwner(own);
                });
                setIsLoading(false);
            }
        });
        
    }
    
    async function sellNFT() {
        // try to ignore the event
        await nft.once("NFTSellEvent", (i_tokenId, price) => {
            l("in NFTSellEvent FIRST:, i_tokenId:" + i_tokenId + ", price:" + price);
        });

        let priceWei = await ethers.utils.parseEther(listSellPrice.toString());
        // l("Sell Price: " + price+ ", priceWei:" + priceWei);
        setIsLoading(true);
        try {
            await nft.setForSale(tokenId, priceWei);
        } catch (error) {   
            console.log("Sale error:" + error);
            setListSell(false);
            setChangeRow(false);
            setIsLoading(false);
        }

        await nft.once("NFTSellEvent", (i_tokenId, price) => {
            l("in NFTSellEvent:, i_tokenId:" + i_tokenId + ", price:" + price);

            if (parseInt(i_tokenId)===tokenId){
                setPrice(price);
                setListSell(false);
                setChangeRow(false);
                setIsLoading(false);
            }
        });
        
    }
    
    async function cancelSell() {
        await nft.once("NFTSellEvent", (i_tokenId, price) => {
            l("in cancelSell NFTSellEvent FIRST:, i_tokenId:" + i_tokenId + ", price:" + price);
        });
        setIsLoading(true);

        try {
            await nft.cancelSale(tokenId);
        }catch (error) {   
            console.log("cancelSale error:" + error);
            setListSell(false);
            setChangeRow(false);
            setIsLoading(false);
        }  
        
        await nft.once("NFTSellEvent", (i_tokenId, price) => {
            if (parseInt(i_tokenId)===tokenId){
                setPrice(price);
                setListSell(false);
                setChangeRow(false);
                setIsLoading(false);
            }
        });
        
    }

    var priceHtml;
    // l("unit id(%d) owener:%s, account:%s", tokenId, owner, account);
    // l(" type(owener):%s, account:%s", typeof(owner), typeof(account));
    if (owner===account){
        // ------------ the item belong owner ------------ //
        if (listSell){
            priceHtml = [
                <div key="0" className="row list-sell"> 
                    <input type="number" step="0.01" min="0" 
                           id="sellprice" name="sellprice" onChange={event => setListSellPrice(event.target.value)}/>	
                    <Button className="sellbtn" variant="danger" onClick={() => sellNFT(false)}>Sell</Button>          
                    <Button className="close" variant="secondary" onClick={() => setListSell(false)}>X</Button>
                </div>];

        } else if (price > 0){
            if (!changeRow){
                let formattedBalance = ethers.utils.formatUnits(price, 18);
                let ethHtml = "Ξ " + formattedBalance;
                priceHtml = [<div key="0" className="row"> 
                                    <div className="col-5 price">{ethHtml}</div>
                                    <div className="col-7 price-btn">
                                        <Button variant="outline-warning" onClick={() => setChangeRow(true)}>Change</Button>
                                    </div>
                            </div>];
            }else{
                priceHtml = [
                    <div key="price-div" className="row change-price-row"> 
                        <Button key="price-btn"  className="change-price" variant="danger" onClick={() => setListSell(true)}>Price</Button>          
                        <Button key="revoke-btn"  className="revoke-sale" variant="secondary" onClick={() => cancelSell()}>Revoke</Button>
                        <Button key="x-btn"  className="close" variant="secondary" onClick={() => setChangeRow(false)}>X</Button>
                    </div>];
            }
        }else{
            // l("in owner==account else, should price <= 0");
            priceHtml = [<div key="0" className="row"> 
                            <div className="col-5 price">No Sale</div>
                            <div className="col-7 price-btn">
                                <Button variant="outline-danger" onClick={() => setListSell(true)}>List Sell</Button>
                            </div>
                        </div>];
            
        }
    }else{
        // ------------ the item is NOT belong owner ------------ //
        if (price > 0){
            let formattedBalance = ethers.utils.formatUnits(price, 18);
            let ethHtml = "Ξ " + formattedBalance;
            priceHtml = [<div key="0" className="row"> 
                                <div className="col-5 price">{ethHtml}</div>
                                <div className="col-7 buy-btn-row">
                                    <Button variant="success" onClick={async () => {await buyNFT();} }>Buy</Button>
                                </div>
                        </div>];
        
        }else{
            priceHtml = "No Sale";
        }
    }
    const plantPath = "/@" +name;
   
    return (

    <div className="col-md-4 col-sm-6 plant-grid-unit">
        <LoadingBar show={isLoading} />
        <ErrorBox />
        <div className="item">
            <div className="title">{name}</div>
            <div id={videoId} className="imgbox">
                {/* <img src={props.img} alt="" />   */}
            </div>
            {/* <div className="lookit" onClick={() => props.parent(tokenId)}><FontAwesomeIcon icon={["fas", "eye"]} /> <b className="fakehref">Look it!</b></div> */}
            <div className="lookit-div"><Link className="lookit" to={plantPath}><FontAwesomeIcon icon={["fas", "eye"]} /> <b className="fakehref">Look it!</b></Link></div>
            <div className="row">
                <div className="col-4 col-name">Owner</div>
                <div className="col-8">{owner.substring(0,6) + "..." + owner.substring(owner.length-4, owner.length)}</div>
            </div>
            <div className="row">
                <div className="col-4 col-name">Feature</div>
                <div className="col-8">
                    <PlantFeature name={name} />
                </div>
            </div>
            <div className="row price-row">
                <div className="col-4 col-name">Price</div>
                <div className="col-8">
                    {priceHtml}
                </div>
            </div>
        </div>
    </div>
    );
};

export default PlantGridUnit;