import React, { useEffect, useState, useRef, useCallback } from 'react';
import {makeStyles } from "@material-ui/core/styles";
import {WSServer} from './config';

import Slider from "@material-ui/core/Slider";
import Button from "@material-ui/core/Button";

import './css/ControlPanel.scss';
import './css/Switch.scss';

const FanPowerDefault = 50;
const FanDurationDefault = 10;
const LEDOnDefault = false;
const LEDBrightnessDefault = 50;
const PumpDurationDefault = 3;

const useStyles = makeStyles((theme) => ({
    root: {
      width: "100%"
    },
    margin: {
      height: theme.spacing(3)
    }
}));
  
const powerMarks = [{
    value: 0,
    label: "0%"
},{
    value: 50,
    label: "50%"
},{
    value: 100,
    label: "100%"
}];

const durationMarks = [{
    value: 5,
    label: "5 sec"
},{
    value: 10,
    label: "10 sec"
},{
    value: 15,
    label: "15 sec"
}];

const l = console.log;

const ControlPanel = (props) => {
    const classes = useStyles();
    const [fanPower, setFanPower] = useState(FanPowerDefault);
    const [fanDuration, setFanDuration] = useState(FanDurationDefault);
    const [ledBrightness, setLEDBrightness] = useState(LEDBrightnessDefault);
    const [ledOn, setLEDOn] = useState(LEDOnDefault);
    const [pumpDuration, setPumpDuration] = useState(PumpDurationDefault);
    const [fanRunning, setFanRunning] = useState(false);
    const [pumpRunning, setPumpRunning] = useState(false);

    const [isUserCtlLED, setIsUserCtlLED] = useState(false);
    const ws = useRef(null);

    l("WSServer:" + WSServer);

    const name = props.name;

    async function sendFan() {
        console.log("power:" + fanPower +", duration:" + fanDuration);
        let detail = JSON.stringify({"power": fanPower, "duration": fanDuration });
        props.controlHandle("fan", detail);
    }

    async function sendLED() {
        console.log("ledOn:" + ledOn + ", brightness:" + ledBrightness);
        let detail = JSON.stringify({"brightness": ledBrightness, "on": ledOn });
        props.controlHandle("led", detail);
    }

    async function sendPump() {
        console.log("pump duration:" + pumpDuration);
        let detail = JSON.stringify({"duration": pumpDuration });
        props.controlHandle("pump", detail);
    }


    const wsOnMessage = useCallback((evt) => {
    // async function wsOnMessage(evt) {
        var messages = evt.data.split('\n');
        l("messages:", messages);
        for (var i = 0; i < messages.length; i++) {
            if (messages[i].length >0){
                // l("ws.current get message:", messages[i]);
                // l("len( messages[i]):", messages[i].length);
                var plantStatus = JSON.parse(messages[i]);
                l(plantStatus);
                if (plantStatus===null){
                    continue;
                }
                if ('FanOn' in plantStatus){
                    setFanRunning(plantStatus['FanOn']);
                }
                if ('PumpOn' in plantStatus){
                    setPumpRunning(plantStatus['PumpOn']);
                }
                if ('LEDBrightness' in plantStatus){
                    if (!isUserCtlLED){
                        plantStatus['LEDBrightness'] > 0 ? setLEDOn(true): setLEDOn(false);
                        setLEDBrightness(plantStatus['LEDBrightness']);
                    } 
                }
            }
           
        }
    }, [isUserCtlLED]);

    useEffect(() => {
        if (window["WebSocket"]) {
            let wsURL = `${WSServer}/@${name}/ws`;
            l("WSURL: ", wsURL);
            ws.current = new WebSocket(wsURL);

            ws.current.onclose = function (evt) {
                l("ws.current onclose()")
            };
            ws.current.onopen = function(evt) { 
                ws.current.send("sync");
            };
            ws.current.onmessage = wsOnMessage;
            
        } else {
            l("Your Browser Don't Support WebSocket");
        }
        return function cleanup() {
            l("in ws cleanup");
            ws.current.close();
        };
    }, [name, wsOnMessage]);

    return(
        <div id="watering-div">
        {/*---------------FAN---------------*/}
        
        <div className="row">
            <div className="col-3 col-md-2 col-name">Fan</div>
            <div className="col-9 col-md-10">
            {!fanRunning ?
                <div className="row">
                    <div className="col-10 col-md-8">
                        <Slider
                            defaultValue={FanPowerDefault}
                            aria-labelledby="discrete-slider-always"
                            marks={powerMarks}
                            valueLabelDisplay="auto"
                            onChange={(event, newValue)=>{setFanPower(Number(newValue));}}
                        />
                        <Slider    min={5} max={15}
                            defaultValue={FanDurationDefault}
                            aria-labelledby="discrete-slider-always"
                            marks={durationMarks}
                            valueLabelDisplay="auto"
                            onChange={(event, newValue)=>{setFanDuration(Number(newValue));}}
                        />
                    </div>
                    <div className="col-6 col-md-2 flex-center">{3} PLT</div>
                    <div className="col-6 col-md-2 flex-center">
                        <Button
                            variant="contained"
                            color="primary"
                            className={classes.button}
                            onClick={() => { sendFan(); }}
                        >Send</Button>
                    </div>
                </div>
            : <div> Fan is Running... <img src="fan.gif" alt="Fan Running" /> </div>
            }
            </div>
        </div>
        
        {/*--------------- Light ---------------*/}
        <div className="row">
            <div className="col-3 col-md-2 col-name">Light</div>
            <div className="col-9 col-md-10">
                <div className="row">
                    <div className="col-4 col-md-2">
                        <label className="switch">
                            <input type="checkbox" checked={ledOn} onChange={(e) => {
                                                setLEDOn(e.target.checked);
                                                setIsUserCtlLED(true);
                                            }}/>
                            <span className="slider" />
                        </label>
                    </div>
                    { ledOn ? (
                        <div className="col-10 col-md-6">
                            <Slider
                                // defaultValue={LEDBrightnessDefault}
                                value={ledBrightness}
                                aria-labelledby="discrete-slider-always"
                                marks={powerMarks}
                                valueLabelDisplay="auto"
                                onChange={(event, newValue)=>{
                                    setLEDBrightness(Number(newValue));
                                    setIsUserCtlLED(true);
                                }}
                            />
                        </div> ): ""
                     }
                    <div className="col-3 col-md-2 flex-center">{3} PLT</div>
                    <div className="col-4 col-md-2 flex-center">
                        <Button
                            variant="contained"
                            color="primary"
                            className={classes.button}
                            onClick={() => { 
                                sendLED(); 
                                setIsUserCtlLED(false);
                            }}
                        >Send</Button>
                    </div>
                </div>
            </div>
        </div>
        {/*---------------Pump---------------*/}
        <div className="row">
            <div className="col-3 col-md-2 col-name">Watering</div>
            <div className="col-9 col-md-10">
            {!pumpRunning ?
                <div className="row">
                    <div className="col-10 col-md-8">
                        <Slider    min={2} max={4}
                            defaultValue={PumpDurationDefault}
                            aria-labelledby="discrete-slider-always"
                            marks={durationMarks}
                            valueLabelDisplay="auto"
                            onChange={(event, newValue)=>{setPumpDuration(Number(newValue));}}
                        />
                    </div>
                    <div className="col-6 col-md-2 flex-center">{100} PLT</div>
                    <div className="col-6 col-md-2 flex-center">
                        <Button
                            variant="contained"
                            color="primary"
                            className={classes.button}
                            onClick={() => { sendPump(); }}
                        >Send</Button>
                    </div>
                </div>
            : <div> Pump is Running... <img src="pump.gif" alt="Pump Running" /> </div>
            }
            </div>
        </div>
        </div>
    );
};

export default ControlPanel;
