export function GetEthNetName(){ 
    if (EthProvider.includes("mainnet.")){
        return "Mainnet"
    }else if (EthProvider.includes("rinkeby.")){
        return "Rinkeby"
    }else if (EthProvider.includes("ropsten.")){
        return "Ropsten"
    }else if (EthProvider.includes("localhost")){
        return "localhost"
    }else{
        return "strange"
    }
}