
import React from 'react'
import ReactDOM from 'react-dom'
import Alert from 'react-bootstrap/Alert';
// type Props = {
//   children: React.ReactChild
//   closeModal: () => void
// }

const TopAlert = React.memo(({ children, closeModal }: Props) => {
// const Modal = () => {
  const domEl = document.getElementById('top-alert')

  if (!domEl) return null
  
  // This is where the magic happens -> our modal div will be rendered into our 'modal-root' div, no matter where we
  // use this component inside our React tree
  return ReactDOM.createPortal(
    // <div>
    //   <button onClick={closeModal}>Close</button>
    //   {children}
    // </div>,
    <Alert variant="danger" onClose={closeModal} dismissible style={{marginBottom: 0}} >
      {/* <Alert.Heading>Oh snap! You got an error!</Alert.Heading> */}
       {children}
    </Alert>,
    domEl
  )
});

export default TopAlert;