import React from 'react';
import './css/PageTail.scss';
export default function LoadingMessage(props){ 
    if (!props.show){return null;}
    return(
        <div className="loading-message">
            {/* <ReactLoading type={"bars"} color={"#ccc"} height={'20%'} width={'20%'} /> */}
            <img src="loading-bars.svg" height='30%' width='30%' alt="loading-bar" />
            <h2>{props.message}</h2>        
        </div>
    )
};