import {VideoPrefix} from "./config.js" 

export function playRemote(name, videoId){
    if (name==='' || name ===undefined){
        return;
    }
    let pc = new RTCPeerConnection({ sdpSemantics : "unified-plan" })
    // let pc = new RTCPeerConnection ({ sdpSemantics : "plan-b" });
    // pc.addTransceiver('video')
    // pc.addTransceiver('video')
    pc.addTransceiver('video', {direction: "recvonly"})

    pc.ontrack = function (event) {
        var el = document.createElement(event.track.kind)
        el.srcObject = event.streams[0]
        el.autoplay = true
        el.controls = true
        el.muted = true
        el.setAttribute('autoplay', '');
        el.setAttribute('muted', '');
        el.setAttribute('playsInline', '');

        el.className = "videoFull"
        // document.getElementById(name + 'Video').appendChild(el)
        document.getElementById(videoId).appendChild(el)
    }

    pc.createOffer()
    .then(offer => {
        pc.setLocalDescription(offer)
        // console.log("offer:");
        // console.log(JSON.stringify(offer));
        return fetch(VideoPrefix + name +'/video', {
            method: 'POST',
            headers: {
                'Accept': 'application/json, text/plain, */*',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(offer)
        })
    })
    .then(res => {
        console.log(`res:`);
        console.log(res);
        return res.json();
    })
    .then(res => pc.setRemoteDescription(res))
    .catch(alert)

    return pc;
}


export function shortAddress(addr){
    return  addr.substring(0,6) + "..." + addr.substring(addr.length-4, addr.length);
}

export function scrollToTargetAdjusted(elId){
    var element = document.getElementById(elId);
    var headerOffset = 50;
    // console.log("element");
    // console.log(element.getBoundingClientRect());

    var elementPosition = element.getBoundingClientRect().top;
    var offsetPosition = elementPosition - headerOffset;

    window.scrollTo({
         top: offsetPosition,
         behavior: "smooth"
    });
}
