import React, { useState, useEffect, useImperativeHandle } from 'react';
// util
import { ts2date, ts2datetime } from './ts.js';
import {VideoServer} from "./config.js" 
import './css/Album.scss';
import OverlayPhoto from "./OverlayPhoto.js"
import { scrollToTargetAdjusted } from './util.js';

const l = console.log;

// var prefix = "";
// var suffix = "";
// const Album =  forwardRef((props, ref) => {
var Album = React.forwardRef((props, ref) => {
    const [photoTsAry, setPhotoTsAry] = useState([]);
    const [, setUpdateTs] = useState();
    const [showOverlayPhoto, setShowOverlayPhoto] = useState(false);
    const [photoCurrentTs, setPhotoCurrentTs] = useState(0);
    const [foucusImgId, setFocusImgId] = useState("");

    const [prefix, setPrefix] = useState(null);
    const [suffix, setSuffix] = useState(null);
    

    const takePhotoUpdate = (imgTs) => {
        setUpdateTs(Date.now());
        setFocusImgId(imgTs);
    };
    let { name, photoURL } = props;

    useEffect(() => {
        // fetch(VideoPrefix + name + "/photo/list")
        fetch(photoURL)
        .then(res => res.json())
        .then(
            (result) => {
                if (result['photos']==null|| result['photos'].length <=0){
                    return;
                }
                if (suffix===null || prefix===null){
                    setSuffix(result['photos'][0].split('.')[1]);
                    setPrefix(result['prefix']);
                }                
               
                let  tsAry = [];
                for (const f of result['photos']) {
                    let fSplit = f.split('.');
                    let ts = parseInt(fSplit[0]);
                    tsAry.push(ts);
                }
                // descending
                tsAry.sort(function(a, b){return b-a});
                setPhotoTsAry(tsAry);
            },
            // Note: it's important to handle errors here
            // instead of a catch() block so that we don't swallow
            // exceptions from actual bugs in components.
            (error) => {
                // setIsLoaded(true);
                // setError(error);
                l(error);
            }
        )
    }, [photoURL, suffix, prefix]);

    useEffect(() => {
        console.log("useEffect foucusImgId!")
        if (foucusImgId!==""){
            // const element = document.getElementById(foucusImgId);
            // element.scrollIntoView({behavior: 'smooth'});
            scrollToTargetAdjusted(foucusImgId);
        }
    }, [foucusImgId]);

    useImperativeHandle(ref, () => ({

        addPhoto: (newPhoto) => {
            if (!newPhoto['success']){
                console.error("Add photo error, err:" + newPhoto['detail']);
                return ;
            }

            let fSplit = newPhoto['detail'].split('.');
            let ts = parseInt(fSplit[0]);
            let dateStr = ts2date(ts);
            if (!photoTsAry.hasOwnProperty(dateStr)){
                photoTsAry[dateStr] = [];
            }
            
            const newArray = [ts].concat(photoTsAry);
            setPhotoTsAry(newArray);
            return {
                cleanValue: takePhotoUpdate(ts)
            }
        }
    
    }));

    function showPhoto(phototTs){
        setPhotoCurrentTs(phototTs);
        setShowOverlayPhoto(true);
    }

    // array to map
    let tmpDatePhotoDic = {};
    let preDate = "";
    var datePhotoAry = NaN;
    for (let i = 0; i < photoTsAry.length; i++) {
        let ts = photoTsAry[i];
        let dateStr = ts2date(ts);
        // l(dateStr);
        if (dateStr!==preDate){
            datePhotoAry = [];//new Array();
            // tmpDatePhotoDic.set(dateStr, datePhotoAry);
            tmpDatePhotoDic[dateStr] = datePhotoAry;
            preDate = dateStr;
        }
        // let photoUrl = prefix + String(ts) + "." + suffix;
        datePhotoAry.push(ts);
    }

    // prepare datePhotosHtml
    let datePhotosHtml = [];
    for (var dateStr in tmpDatePhotoDic) {
        datePhotosHtml.push( <div key={dateStr} className="caption">{dateStr}</div>)
        
        for (const ts of tmpDatePhotoDic[dateStr]) {
            // console.log(dateStr + " -> " + url);
            let photoUrl = VideoServer + prefix + String(ts) + "." + suffix;
            datePhotosHtml.push( <img id={ts} key={ts} alt={name+"_"+ts} className="photo" src={photoUrl} onClick={() => showPhoto(ts)}  />)
        }
    }

    // for OverlayPhoto
    let photoCurrentTsIdx = photoCurrentTs > 0 ? photoTsAry.indexOf(photoCurrentTs):0;
    let description= photoCurrentTs > 0 ? ts2datetime(photoCurrentTs, true) : "";
    let currentUrl = photoCurrentTs > 0 ? (VideoServer + prefix + String(photoCurrentTs) + "." + props.overlaySuffix)  : "";
    let preFunc = (photoCurrentTsIdx>0) ?  () => showPhoto( photoTsAry[photoCurrentTsIdx-1] )  : null;
    let nextFunc = (photoCurrentTsIdx < (photoTsAry.length-1))  ?  () => showPhoto(photoTsAry[photoCurrentTsIdx+1] )  : null;
    return (
        <>
        <div className="album"> {datePhotosHtml} </div>
        <OverlayPhoto show={showOverlayPhoto} handleClose={() => setShowOverlayPhoto(false)}
                    current={currentUrl}
                    handlePrevious={preFunc} 
                    handleNext={nextFunc}
                    description={description} />
        </>
    )
});
Album.displayName = 'Album';
export default Album;