import React from "react";
import { ethers } from "ethers";
import PlantNFT from "./contracts/PlantNFT.json";
import {EthProvider, contractAddress} from "./config.js"
import detectEthereumProvider from '@metamask/detect-provider';
import TagManager from 'react-gtm-module'
const login_args = {
  dataLayer: {
    event: "login_eth"
    /* can pass more variables here if needed */
  },
  dataLayerName: "PageDataLayer"
};


export const ethLink = {
    provider : null, //new ethers.providers.JsonRpcProvider(EthProvider),
    signer : null, 
    nft: null,
    account: '',
    noneAccountFetch: function(){
        this.provider = new ethers.providers.JsonRpcProvider(EthProvider);
        this.nft = new ethers.Contract(contractAddress, PlantNFT.abi, this.provider);
        this.appSetNft(this.nft);
    },
    initEth : async function(appSetAccount, appSetNft) {
        this.appSetAccount = appSetAccount;
        this.appSetNft = appSetNft;
        console.log('in chkProvider()!');
        this.noneAccountFetch();
            
        let resProvider = await detectEthereumProvider(false, false, 2); //.then( resProvider =>{
        if (resProvider) {
            // From now on, this should always be true:
            // resProvider === window.ethereum
            window.ethereum.on('accountsChanged', (accounts) => {
                console.log("accounts:" );
                console.log(accounts);
                if (accounts !== undefined && accounts.length>0){
                    this.login(resProvider);
                }
            });
        }
    },
    login: async function(resProvider) {
        try {
            console.log("resProvider");
            console.log(resProvider);
            if (resProvider===undefined){
                console.log("in resProvider===undefined");
                const provider = await detectEthereumProvider(false, false, 2);
                if (provider) {
                // From now on, this should always be true:
                // provider === window.ethereum
                    this.provider = new ethers.providers.Web3Provider(provider);
                    window.ethereum.on('accountsChanged', (accounts) => {
                        console.log("accounts:" + String(accounts));
                        // this.login();
                        if (accounts !== undefined && accounts.length>0){
                            this.login();
                        }
                    });
                } else {
                    console.log('Please install MetaMask!');
                    throw new Error('You don\'t have MetaMask, please install it!'); 
                }
            }else{
                this.provider = new ethers.providers.Web3Provider(resProvider);
            }
            
            // console.log("this.provider:");
            // console.log(this.provider);
            
            
            this.signer = this.provider.getSigner();
            this.account = await this.signer.getAddress();
            this.nft = new ethers.Contract(contractAddress, PlantNFT.abi, this.signer);

            this.appSetAccount(this.account);
            this.appSetNft(this.nft);

            console.log("before login_args")
            console.log(login_args);
            // TagManager.dataLayer(login_args);
            TagManager.dataLayer({
                dataLayer: {
                    event: "login_eth"
                }
            })
            
            return this.account;
        } catch (e) {
            // console.log(e.name + ': ' + e.message);
            throw e;
        }
        
    },

};
  
export const EthContext = React.createContext();
  