import React, { useState, useEffect } from 'react';
import './css/OverlayPhoto.css';
// const l = console.log;
// export default function OverlayPhoto(props){ 
// const OverlayPhoto = (props, ref) => {
const OverlayPhoto = (props) => {
    const [current, setCurrent] = useState("");

    useEffect(() => {
        setCurrent(props.current);
    }, [props]);
    if (!props.show) return null;
    let preHtml = props.handlePrevious!=null ? 
                <div className="overlay-arrow" onClick={props.handlePrevious} ><h1>&lt;</h1></div> : 
                <div className="overlay-arrow" /> ;
    let nextHtml = props.handleNext!=null ? 
                <div className="overlay-arrow" onClick={props.handleNext} ><h1>&gt;</h1></div> : 
                <div className="overlay-arrow" /> ;
     
    return(
        <div id="myNav" className="overlay">
            <h1 className="closebtn" onClick={props.handleClose}>&times;</h1>
            {/* <div className="closebtn" onClick={props.handleClose}>&times;</div> */}
            <div className="overlay-content">
                {preHtml}
                <div className="overlay-photo">
                    { current.endsWith("jpg") ? 
                        <img alt={props.description}  src={current} />
                    : "" }
                    { current.endsWith("mp4") ? 
                        <video  src={current} type="video/mp4"  width="90%" autoplay controls muted>
                            Your browser does not support the video tag, please upgrade the
                        </video>
                    : "" }

                    <div className="date">{props.description}</div>
                </div>
                {nextHtml}
            </div>
        </div>
    );
};


export default OverlayPhoto;
