import React, { useState, useEffect } from 'react';
import PlantGridUnit from "./PlantGridUnit"
import {EthContext} from "./EthContext.js";
//css
import './css/LoadingMessage.scss';
import LoadingMessage from './LoadingMessage';
import { GetEthNetName } from './config';
const l = console.log;

const PlantGrid = (props) => {
    const [totalSupply, setTotalSupply] = useState(0);
    const {nft} = React.useContext(EthContext);
    // const nft = ethCxt.getNft();
    // let account = props.account;
    // Similar to componentDidMount and componentDidUpdate:
    useEffect(() => {
        l('in PlantGrid useEffect(), nft:', nft);
        // if (nft!==undefined){
        //     l('in PlantGrid useEffect(), nft.totalSupply():', nft.totalSupply());
        // }
        
        if (nft!=null){
            nft.totalSupply().then(totalSupply => {
                l("totalSupply:" , totalSupply);
                setTotalSupply(totalSupply);
            });
        }
        // Update the document title using the browser API
        return function cleanup() {
          l('in useEffect cleanup()');
        };
    }, [nft]);

    // console.log("in PlantGrid!!");
    const childCall = (tokenId) => {
        l("PlantGrid childcall tokenId:" + tokenId);
    };
    // l("grid, account:%s", account);
    var i;
    const items = [];
    for (i = 0; i < totalSupply; i++) {
        items.push(<PlantGridUnit key={i} id={i} parent={childCall} />);
    }
    if (items.length<=0){
        const msg = `Connect to Ethereum ${GetEthNetName()} ...`
        items.push( <LoadingMessage key="loading-ethereum" show={true} message={msg} />);
    }
   

    return(
        <div className="row gallery">
            {items}
        </div>
    );
};

export default PlantGrid;
