import React, {useState, useEffect} from 'react';
import Navigation from "./Navigation"
import './css/app.scss';
import {
    Switch,
    Route  
} from "react-router-dom";
import PlantGrid from "./PlantGrid"
import Plant from "./Plant"
import PageTail from "./PageTail"
import usePageTracking from "./usePageTracking.js"
import {ethLink, EthContext} from "./EthContext.js"
// const l = console.log;

function App() {
    const [nft, setNft] = useState(ethLink.setNft);
    const [account, setAccount] = useState(ethLink.account);
    // const msgCxt = useContext(MsgBoxContext);
    usePageTracking();
    useEffect(() => {
        ethLink.initEth(setAccount, setNft);
    }, []);

    // l("in App")
  
    return (
        <div className="app">
        
            <div id='top-alert' />
            <EthContext.Provider
                value={{ nft, setNft, account, setAccount, ethLink }}>
                <Navigation />
                <div className="container">    
                    <Switch>
                        <Route exact path="/">
                            <PlantGrid  />
                        </Route>
                        <Route path="/@:name" children={<Plant />} />      
                    </Switch>
                </div>
            </EthContext.Provider>
            <PageTail />
        </div>
);
};

export default App;
